"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { FileText, Download, Loader2 } from "lucide-react"
import { jsPDF } from "jspdf"

interface OMGeneratorProps {
  listing: any
}

export function OMMemorandumGenerator({ listing }: OMGeneratorProps) {
  const [open, setOpen] = useState(false)
  const [generating, setGenerating] = useState(false)

  const generatePDF = async () => {
    setGenerating(true)

    try {
      const pdf = new jsPDF({
        orientation: "portrait",
        unit: "pt",
        format: "letter",
      })

      const pageWidth = pdf.internal.pageSize.getWidth()
      const pageHeight = pdf.internal.pageSize.getHeight()
      const margin = 40

      // Helper function to load images
      const loadImage = (url: string): Promise<HTMLImageElement> => {
        return new Promise((resolve, reject) => {
          const img = new Image()
          img.crossOrigin = "anonymous"
          img.onload = () => resolve(img)
          img.onerror = reject
          img.src = url
        })
      }

      // PAGE 1: Cover Page
      // Background - dark blue/slate
      pdf.setFillColor(30, 41, 59) // slate-800
      pdf.rect(0, 0, pageWidth, pageHeight, "F")

      // Company Logo at top
      try {
        const logoImg = await loadImage(
          "https://encrypted-tbn3.gstatic.com/images?q=tbn:ANd9GcTirZvs6mwvxX4xSRx9pPSYetcWbXdcuqs_o5hVVBk1e_BGdj6Q",
        )
        const logoWidth = 80
        const logoHeight = (logoImg.height / logoImg.width) * logoWidth
        pdf.addImage(logoImg, "PNG", (pageWidth - logoWidth) / 2, 40, logoWidth, logoHeight)
      } catch (error) {
        console.error("Failed to load logo:", error)
      }

      // Property Image with white border
      if (listing.thumbnail_url) {
        try {
          const propImg = await loadImage(listing.thumbnail_url)
          const imgWidth = pageWidth - 2 * margin
          const imgHeight = 300
          const imgY = 140

          // White border
          pdf.setFillColor(255, 255, 255)
          pdf.rect(margin - 5, imgY - 5, imgWidth + 10, imgHeight + 10, "F")

          pdf.addImage(propImg, "JPEG", margin, imgY, imgWidth, imgHeight)
        } catch (error) {
          console.error("Failed to load property image:", error)
        }
      }

      // Diagonal overlay for "FOR SALE" text
      pdf.setFillColor(0, 0, 0)
      pdf.setGState(new pdf.GState({ opacity: 0.7 }))
      const triangleY = 460
      pdf.triangle(0, triangleY, pageWidth, triangleY, pageWidth, pageHeight, "F")
      pdf.setGState(new pdf.GState({ opacity: 1 }))

      // FOR SALE/LEASE text
      pdf.setTextColor(255, 255, 255)
      pdf.setFontSize(48)
      pdf.setFont("helvetica", "bold")
      const listingTypeText =
        listing.listing_type === "for_sale"
          ? "FOR SALE"
          : listing.listing_type === "for_lease"
            ? "FOR LEASE"
            : "FOR SALE OR LEASE"
      pdf.text(listingTypeText, pageWidth / 2, 520, { align: "center" })

      // Property details
      pdf.setFontSize(16)
      pdf.setFont("helvetica", "normal")
      const detailsLine1 = `${listing.building_sf ? listing.building_sf.toLocaleString() + " SF" : ""} ${listing.categories?.name || ""}`
      pdf.text(detailsLine1.trim(), pageWidth / 2, 555, { align: "center" })

      const addressLine = `${listing.address} | ${listing.city}, ${listing.state} ${listing.zip_code}`
      pdf.setFontSize(14)
      pdf.text(addressLine, pageWidth / 2, 580, { align: "center" })

      // Key stats boxes at bottom
      const boxY = 640
      const boxWidth = (pageWidth - 3 * margin) / 3
      const boxHeight = 80

      // Property Size Box
      pdf.setFillColor(255, 255, 255)
      pdf.rect(margin, boxY, boxWidth, boxHeight, "F")
      pdf.setTextColor(100, 116, 139)
      pdf.setFontSize(10)
      pdf.setFont("helvetica", "bold")
      pdf.text("PROPERTY SIZE", margin + boxWidth / 2, boxY + 20, { align: "center" })
      pdf.setTextColor(30, 41, 59)
      pdf.setFontSize(18)
      const propSize = listing.lot_acres
        ? `${listing.lot_acres} ACRES`
        : `${listing.building_sf?.toLocaleString() || "N/A"} SF`
      pdf.text(propSize, margin + boxWidth / 2, boxY + 50, { align: "center" })

      // Traffic Count Box (if available)
      const trafficX = margin + boxWidth + margin / 2
      pdf.setFillColor(255, 255, 255)
      pdf.rect(trafficX, boxY, boxWidth, boxHeight, "F")
      pdf.setTextColor(100, 116, 139)
      pdf.setFontSize(10)
      pdf.text("TRAFFIC COUNT", trafficX + boxWidth / 2, boxY + 20, { align: "center" })
      pdf.setTextColor(30, 41, 59)
      pdf.setFontSize(18)
      const trafficCount = listing.custom_fields?.traffic_count || "N/A"
      pdf.text(trafficCount, trafficX + boxWidth / 2, boxY + 50, { align: "center" })

      // Pricing Box
      const priceX = trafficX + boxWidth + margin / 2
      pdf.setFillColor(255, 255, 255)
      pdf.rect(priceX, boxY, boxWidth, boxHeight, "F")
      pdf.setTextColor(100, 116, 139)
      pdf.setFontSize(10)
      pdf.text("PRICING", priceX + boxWidth / 2, boxY + 20, { align: "center" })
      pdf.setTextColor(30, 41, 59)
      pdf.setFontSize(18)
      pdf.text(`$${listing.list_price?.toLocaleString() || "N/A"}`, priceX + boxWidth / 2, boxY + 50, {
        align: "center",
      })

      // PAGE 2: Investment Summary
      pdf.addPage()
      pdf.setFillColor(248, 250, 252) // slate-50
      pdf.rect(0, 0, pageWidth, pageHeight, "F")

      // Header
      pdf.setFillColor(30, 58, 95) // dark blue
      pdf.rect(0, 0, pageWidth, 100, "F")
      pdf.setTextColor(255, 255, 255)
      pdf.setFontSize(32)
      pdf.setFont("helvetica", "bold")
      pdf.text("INVESTMENT SUMMARY", pageWidth / 2, 60, { align: "center" })

      // Three stats boxes
      const statsY = 130
      const statsBoxW = 160
      const statsBoxH = 120
      const statsSpacing = (pageWidth - 3 * statsBoxW - 2 * margin) / 2

      // Property Size
      pdf.setFillColor(255, 255, 255)
      pdf.rect(margin, statsY, statsBoxW, statsBoxH, "F")
      pdf.setTextColor(71, 85, 105)
      pdf.setFontSize(10)
      pdf.setFont("helvetica", "bold")
      pdf.text("PROPERTY SIZE", margin + statsBoxW / 2, statsY + 25, { align: "center" })
      pdf.setTextColor(15, 23, 42)
      pdf.setFontSize(20)
      pdf.setFont("helvetica", "bold")
      pdf.text(propSize, margin + statsBoxW / 2, statsY + 70, { align: "center" })

      // Traffic Count
      const stats2X = margin + statsBoxW + statsSpacing
      pdf.setFillColor(255, 255, 255)
      pdf.rect(stats2X, statsY, statsBoxW, statsBoxH, "F")
      pdf.setTextColor(71, 85, 105)
      pdf.setFontSize(10)
      pdf.text("TRAFFIC COUNT", stats2X + statsBoxW / 2, statsY + 25, { align: "center" })
      pdf.setTextColor(15, 23, 42)
      pdf.setFontSize(20)
      pdf.text(trafficCount, stats2X + statsBoxW / 2, statsY + 70, { align: "center" })

      // Pricing
      const stats3X = stats2X + statsBoxW + statsSpacing
      pdf.setFillColor(255, 255, 255)
      pdf.rect(stats3X, statsY, statsBoxW, statsBoxH, "F")
      pdf.setTextColor(71, 85, 105)
      pdf.setFontSize(10)
      pdf.text("PRICING", stats3X + statsBoxW / 2, statsY + 25, { align: "center" })
      pdf.setTextColor(15, 23, 42)
      pdf.setFontSize(20)
      pdf.text(`$${listing.list_price?.toLocaleString() || "N/A"}`, stats3X + statsBoxW / 2, statsY + 70, {
        align: "center",
      })

      // Investment Details Section
      let yPos = 280
      pdf.setTextColor(30, 41, 59)
      pdf.setFontSize(16)
      pdf.setFont("helvetica", "bold")
      pdf.text("INVESTMENT DETAILS:", margin, yPos)

      yPos += 30

      // Two columns
      const colWidth = (pageWidth - 3 * margin) / 2

      // Property Overview (left column)
      pdf.setFontSize(12)
      pdf.setFont("helvetica", "bold")
      pdf.text("Property Overview:", margin, yPos)

      yPos += 20
      pdf.setFontSize(10)
      pdf.setFont("helvetica", "normal")
      pdf.setTextColor(51, 65, 85)

      if (listing.description) {
        const lines = pdf.splitTextToSize(listing.description, colWidth - 10)
        pdf.text(lines.slice(0, 15), margin, yPos) // Limit to prevent overflow
      }

      // Property Features (right column)
      const rightColX = margin + colWidth + margin
      yPos = 310
      pdf.setFontSize(12)
      pdf.setFont("helvetica", "bold")
      pdf.setTextColor(30, 41, 59)
      pdf.text("Property Features:", rightColX, yPos)

      yPos += 20
      pdf.setFontSize(10)
      pdf.setFont("helvetica", "normal")
      pdf.setTextColor(51, 65, 85)

      const features = [
        `Pricing: $${listing.list_price?.toLocaleString() || "N/A"}`,
        `Property size: ${listing.lot_acres ? listing.lot_acres + " acres" : ""}`,
        `Building: ${listing.building_sf ? listing.building_sf.toLocaleString() + " SF" : "N/A"}`,
        `Traffic count: ${trafficCount}`,
        `Utilities: ${listing.custom_fields?.utilities || "Available"}`,
        `Zoning: ${listing.zoning || "N/A"}`,
        `Year Built: ${listing.year_built || "N/A"}`,
      ]

      features.forEach((feature) => {
        pdf.text(` ${feature}`, rightColX, yPos)
        yPos += 18
      })

      // PAGE 3: Investment Highlights with Samuel's Image
      pdf.addPage()
      pdf.setFillColor(30, 41, 59) // dark background
      pdf.rect(0, 0, pageWidth, pageHeight, "F")

      // Investment Highlights Header
      pdf.setTextColor(255, 255, 255)
      pdf.setFontSize(20)
      pdf.setFont("helvetica", "bold")
      pdf.text("INVESTMENT HIGHLIGHTS:", margin, 60)

      // Highlights list
      yPos = 100
      pdf.setFontSize(11)
      pdf.setFont("helvetica", "normal")

      const highlights = [
        `High-visibility location in ${listing.city}, ${listing.state}`,
        `Prime ${listing.categories?.name || "commercial"} property`,
        `${listing.building_sf ? listing.building_sf.toLocaleString() + " SF" : ""} of space`,
        `Zoned ${listing.zoning || "for commercial use"}`,
        `Excellent investment opportunity`,
        `Strong market presence`,
      ]

      highlights.forEach((highlight) => {
        pdf.text(` ${highlight}`, margin + 20, yPos)
        yPos += 25
      })

      // Diagonal section with Samuel's image
      pdf.setFillColor(0, 0, 0)
      pdf.setGState(new pdf.GState({ opacity: 0.85 }))
      const diagY = 350
      pdf.triangle(0, diagY, pageWidth, diagY - 100, pageWidth, pageHeight, "F")
      pdf.setGState(new pdf.GState({ opacity: 1 }))

      // Samuel's Image and Contact Info
      try {
        const samImg = await loadImage(
          "https://www.scarboroughcre.com/glide/uploads/cms/67e15b77743ec/?w=400&h=400&fit=max&or=auto",
        )
        const samImgSize = 120
        const samImgX = pageWidth - margin - samImgSize - 100
        const samImgY = pageHeight - margin - samImgSize - 120

        pdf.addImage(samImg, "JPEG", samImgX, samImgY, samImgSize, samImgSize)

        // Contact info
        const contactX = samImgX + samImgSize + 20
        const contactY = samImgY + 20

        pdf.setTextColor(255, 255, 255)
        pdf.setFontSize(14)
        pdf.setFont("helvetica", "bold")
        pdf.text("INVESTMENT CONTACT:", contactX, contactY)

        pdf.setFontSize(12)
        pdf.text("Samuel Scarborough, CCIM", contactX, contactY + 25)

        pdf.setFontSize(10)
        pdf.setFont("helvetica", "normal")
        pdf.text("Broker/President", contactX, contactY + 45)
        pdf.text("(903) 570-7366", contactX, contactY + 65)
        pdf.text("www.scarboroughcre.com", contactX, contactY + 85)
      } catch (error) {
        console.error("Failed to load Samuel's image:", error)
      }

      // PAGE 4: Property Images
      if (listing.files && listing.files.length > 0) {
        pdf.addPage()
        pdf.setFillColor(248, 250, 252)
        pdf.rect(0, 0, pageWidth, pageHeight, "F")

        // Header
        pdf.setTextColor(30, 41, 59)
        pdf.setFontSize(24)
        pdf.setFont("helvetica", "bold")
        pdf.text("PROPERTY IMAGES", pageWidth / 2, 60, { align: "center" })

        // Display images in grid (2x3)
        const imageFiles = listing.files.filter((f: any) => f.file_type?.startsWith("image/"))
        const imgStartY = 100
        const imgWidth = (pageWidth - 3 * margin) / 2
        const imgHeight = 180
        const imgSpacing = 20

        let imgX = margin
        let imgY = imgStartY
        let imgCount = 0

        for (const file of imageFiles.slice(0, 6)) {
          try {
            const img = await loadImage(file.file_url)

            pdf.setFillColor(255, 255, 255)
            pdf.rect(imgX - 5, imgY - 5, imgWidth + 10, imgHeight + 10, "F")

            pdf.addImage(img, "JPEG", imgX, imgY, imgWidth, imgHeight)

            imgCount++

            // Move to next position
            if (imgCount % 2 === 0) {
              imgX = margin
              imgY += imgHeight + imgSpacing
            } else {
              imgX += imgWidth + margin
            }

            // Don't overflow page
            if (imgY + imgHeight > pageHeight - margin) break
          } catch (error) {
            console.error("Failed to load property image:", error)
          }
        }
      }

      // Save PDF
      const fileName = `OM_${listing.address.replace(/[^a-z0-9]/gi, "_")}.pdf`
      pdf.save(fileName)
    } catch (error) {
      console.error("Error generating PDF:", error)
      alert("Failed to generate PDF. Please try again.")
    } finally {
      setGenerating(false)
    }
  }

  return (
    <>
      <Button onClick={() => setOpen(true)} className="bg-emerald-600 hover:bg-emerald-700 text-white">
        <FileText className="w-4 h-4 mr-2" />
        Generate OM Memorandum
      </Button>

      <Dialog open={open} onOpenChange={setOpen}>
        <DialogContent className="max-w-md">
          <DialogHeader>
            <DialogTitle>Generate Offering Memorandum</DialogTitle>
          </DialogHeader>

          <div className="space-y-4">
            <p className="text-sm text-gray-600">
              Generate a professional Offering Memorandum (OM) PDF for this listing including property details,
              investment highlights, and images.
            </p>

            <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
              <h4 className="font-semibold text-sm text-blue-900 mb-2">Document will include:</h4>
              <ul className="text-sm text-blue-800 space-y-1">
                <li> Cover page with property image</li>
                <li> Investment summary with key metrics</li>
                <li> Investment highlights</li>
                <li> Property images gallery</li>
              </ul>
            </div>

            <div className="flex gap-2">
              <Button
                onClick={generatePDF}
                disabled={generating}
                className="flex-1 bg-emerald-600 hover:bg-emerald-700"
              >
                {generating ? (
                  <>
                    <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                    Generating...
                  </>
                ) : (
                  <>
                    <Download className="w-4 h-4 mr-2" />
                    Generate PDF
                  </>
                )}
              </Button>
              <Button onClick={() => setOpen(false)} variant="outline">
                Cancel
              </Button>
            </div>
          </div>
        </DialogContent>
      </Dialog>
    </>
  )
}
